@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    src: url("../fonts/#{$url}.woff2") format("woff2"),
         url("../fonts/#{$url}.woff") format("woff");
    font-style: $style;
    font-weight: #{$weight};
    font-family: "#{$font-family}";

    font-display: swap;
  }
}

@include font-face("e-Ukraine",  "e-Ukraine-Light", 300, normal);
@include font-face("e-Ukraine",  "e-Ukraine-Regular", 400, normal);
@include font-face("e-Ukraine",  "e-Ukraine-Medium", 500, normal);
@include font-face("e-Ukraine",  "e-Ukraine-Bold", 700, normal);

// 100  Thin || Hairline
// 200  Extra Light || Ultra Light || Thin
// 300  Light
// 450  Demi
// 400  Normal || Regular
// 500  Medium
// 600  Semi Bold || Demi Bold
// 700  Bold
// 800  Extra Bold || Ultra Bold || Heavy
// 900  Black || Heavy

